.navigation {
  width: 100%;
  background-color: #81B3D2;
  display: flex;
  flex-direction: column;
  position: relative; }

.navigation__toggle-close {
  position: absolute;
  top: 2px;
  right: 2px;
  z-index: 2;
  display: block;
  width: 53px;
  height: 53px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none; }
  .navigation__toggle-close::before, .navigation__toggle-close::after {
    content: "";
    position: absolute;
    width: 30px;
    height: 4px;
    background-color: white;
    top: 24px;
    right: 14px; }
  .navigation__toggle-close::before {
    transform: rotate(45deg); }
  .navigation__toggle-close::after {
    transform: rotate(-45deg); }
  .navigation__toggle-close:hover::before, .navigation__toggle-close:hover::after {
    opacity: 0.6; }
  .navigation__toggle-close:active::before, .navigation__toggle-close:active::after {
    opacity: 0.3; }

.navigation--nojs .navigation__toggle-close {
  display: none; }

.navigation__burger {
  position: absolute;
  background-color: transparent;
  width: 53px;
  height: 53px;
  bottom: -55px;
  right: 0;
  border: none;
  padding: 0;
  outline: none; }

.navigation__burger::before {
  content: "";
  width: 22px;
  height: 4px;
  background-color: #81B3D2;
  position: absolute;
  top: 15px;
  right: 20px;
  box-shadow: 0 9px 0 0 #81B3D2, 0 18px 0 0 #81B3D2; }

.navigation__burger:hover::before {
